@charset "UTF-8";
@font-face {
  font-family: "quantico";
  src: url(/style/font/quantico/Quantico-Regular.otf) format("truetype");
}
@font-face {
  font-family: "prompt";
  src: url(/style/font/prompt/Prompt-Italic.ttf) format("truetype");
}
button {
  color: white;
  font-size: 1rem;
  font-family: "quantico", sans-serif;
  width: auto;
  height: 100%;
  padding: 0.3rem 0.5rem 0.3rem 0.5rem;
  cursor: pointer;
}

.btnOne {
  background: linear-gradient(to right, #740CDC, #56ADD5);
  border: none;
  border-radius: 15px;
}

.btnTwo {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #0F162A;
  color: white;
  font-weight: bold;
  border-radius: 15px;
  position: relative;
  width: 70px;
  height: 40px;
  text-align: center;
  padding: 5px;
  text-decoration: none;
  outline: none;
  border: 1px solid transparent;
}
.btnTwo::before {
  content: "";
  display: block;
  background: linear-gradient(to top left, #740CDC, #56ADD5);
  position: absolute;
  z-index: -1;
  width: calc(100% + 5px);
  height: calc(100% + 5px);
  padding: 5px;
  border-radius: 20px;
  margin: -1px;
}

.btnThree {
  background: none;
  font-size: 1.25rem;
  border: none;
  color: #7D8296;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: auto;
  padding: 1rem 2rem 0rem 2rem;
}
nav .menu {
  display: flex;
  margin: auto;
  list-style: none;
}
nav .menu a {
  display: flex;
  flex-direction: column;
  margin-left: 3rem;
  font-weight: bold;
  text-decoration: none;
  color: white;
}
nav .menu a span {
  font-size: 0.8rem;
  text-align: right;
}

.menu_toggle {
  display: none;
  flex-direction: column;
  align-items: flex-end;
}
.menu_toggle .barOne {
  margin: 0.1rem;
  width: 3rem;
  height: 0.4rem;
  background-color: white;
  border-radius: 10px;
}
.menu_toggle .barTwo {
  margin: 0.1rem;
  width: 2.6rem;
  height: 0.4rem;
  background-color: white;
  border-radius: 10px;
}
.menu_toggle .barThree {
  margin: 0.1rem;
  width: 2.2rem;
  height: 0.4rem;
  background-color: white;
  border-radius: 10px;
}
.menu_toggle:hover .barOne {
  transform: translateY(-0.3rem);
}
.menu_toggle:hover .barThree {
  transform: translateY(0.3rem);
}
.menu_toggle .barOne, .menu_toggle .barThree {
  transition: transform 0.3s ease;
}

.effectOnOver {
  opacity: 0.5;
  transition: opacity 0.5s ease, transform 0.5s ease;
}

@media screen and (max-width: 1480px) {
  .menu_toggle {
    display: flex;
  }
  nav .menu {
    display: none;
  }
}
:root {
  font-size: 24px;
}

body {
  display: flex;
  flex-direction: column;
  background-color: #0F162A;
  font-family: "quantico", sans-serif;
  line-height: 1;
  margin: auto;
  font-size: 1rem;
}

h1, h2, h3 {
  color: white;
}

h4 {
  color: #589CD5;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

.logo {
  max-width: 75px;
  height: auto;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  line-height: 2.5rem;
  padding: 0rem 2rem 0rem 2rem;
}
.intro_p {
  color: #7D8296;
  font-size: 1.5rem;
}
.intro h1 {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.3rem;
}
.intro button {
  align-self: flex-start;
}

@media screen and (max-width: 1100px) {
  .intro {
    align-items: center;
  }
  .intro h1 {
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
  }
  .intro button {
    align-self: center;
  }
}
.aboutme {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0rem 2rem 0rem 2rem;
}
.aboutme_portait {
  display: flex;
}
.aboutme_img {
  max-width: 500px;
  height: auto;
}
.aboutme_description {
  margin-left: 3rem;
}
.aboutme_description_intro h2 {
  margin: 0;
}
.aboutme_description_intro p {
  display: flex;
  flex-direction: column;
  color: #7D8296;
}
.aboutme_description_intro p span {
  padding-top: 1rem;
}
.aboutme_description_areaBtn {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.aboutme_description_areaBtn button {
  margin-right: 1rem;
}

@media screen and (max-width: 1100px) {
  .aboutme {
    flex-direction: column;
    align-items: center;
  }
  .aboutme_portrait {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .aboutme_description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    margin-left: 0;
  }
  .aboutme_description_intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
}
.portfolio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0rem 2rem 0rem 2rem;
}
.portfolio img {
  max-width: 574px;
  height: auto;
  margin-left: 1rem;
}
.portfolio_menu {
  margin-bottom: 0;
}
.portfolio_project {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 2rem;
}
.portfolio_project_description {
  width: 50%;
  color: white;
}
.portfolio_project_description_p {
  color: #7D8296;
}
.portfolio_project_description_number {
  color: #589CD5;
}
.portfolio_project_description_link {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.portfolio_project_description_link_arrow {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.portfolio_project_description_link_arrow_stick {
  border: 2px solid white;
  width: 40px;
  height: 0;
}
.portfolio_project_description_link_arrow_end {
  display: flex;
  align-items: center;
  margin-right: 1rem;
  height: 0;
  width: 0;
  border-top: 10px solid transparent; /* Hauteur de la flèche */
  border-bottom: 10px solid transparent;
  border-left: 10px solid white; /* Couleur de la flèche */
}
.portfolio_project_img {
  height: 100%;
}
.portfolio_project_img img {
  width: auto;
  max-height: 500px;
}
.portfolio button {
  margin-bottom: 2rem;
}

@media screen and (max-width: 1100px) {
  .portfolio_project {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .portfolio_project_description {
    margin-bottom: 1rem;
  }
  .portfolio_project_description_link {
    justify-content: center;
  }
}
.formations {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
  padding: 0rem 2rem 0rem 2rem;
}
.formations h2 {
  margin-bottom: 3rem;
}
.formations_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 75%;
  gap: 3rem;
}
.formations_list_background {
  background: #0F162A;
  border-radius: 10px;
  margin: 0.5rem;
  padding: 1rem;
  width: 400px;
  height: 300px;
}
.formations_list_frame {
  background: linear-gradient(to right, #56ADD5, #740CDC);
  border-radius: 15px;
}
.formations_list_frame_header {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
.formations_list_frame_header img {
  width: auto;
  max-height: 80px;
  margin-right: 1rem;
}
.formations_list_frame_header_title {
  font-weight: bold;
}
.formations_list_frame_skills_catalogue {
  display: flex;
  align-items: center;
  margin: auto;
  max-width: auto;
  position: relative;
  z-index: 2;
  padding: 1rem;
  box-sizing: border-box;
  color: #FFF;
  background: #0F162A;
  background-clip: padding-box; /* !importanté */
  border: solid 20px transparent; /* !importanté */
  border-radius: 1em;
}
.formations_list_frame_skills_catalogue::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -20px; /* !importanté */
  border-radius: inherit; /* !importanté */
  background: linear-gradient(to right, #56ADD5, #740CDC);
}

.technologies {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0rem 2rem 0rem 2rem;
}
.technologies_div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}
.technologies_div_list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0rem 5rem 2rem 5rem;
}
.technologies_div_list img {
  max-width: 200px;
  max-height: auto;
}
.technologies_div_list p {
  color: #7D8296;
  font-size: 1.5rem;
  font-weight: bold;
}

.xp {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0rem 2rem 0rem 2rem;
}
.xp h2 {
  margin-bottom: 3rem;
}
.xp_list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}
.xp_list_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to right, #56ADD5, #740CDC);
  border-radius: 15px;
  padding: 0rem 1rem 0rem 1rem;
  margin-bottom: 1rem;
  gap: 60px;
  width: 75%;
  height: auto;
}
.xp_list_description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background: #4A3CC5;
  border-radius: 15px;
  padding: 1rem;
  margin-bottom: 2rem;
  width: 75%;
  height: auto;
}
.xp_list_description_text {
  margin-bottom: 1rem;
}
.xp_list_description_contact {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 70px;
}
.xp_list_description_contact_adress {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  color: #B4B7C9;
}
.xp_list_description_contact_email {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}
.xp_list_description_contact_email a {
  text-decoration: none;
  color: #B4B7C9;
}
.xp_list_description_contact img {
  max-width: 20px;
  height: 100%;
}
.xp_list_description_skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}
.xp_list_description_skills p {
  background: #2C3599;
  padding: 0.5rem;
  border-radius: 15px;
  margin: 0rem 1rem 0.5rem 0rem;
  text-align: center;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 200px 200px 0px 0px;
  margin-top: 2rem;
  background: linear-gradient(to bottom right, #740CDC, #56ADD5);
  padding: 0rem 2rem 1rem 2rem;
}
.footer h2 {
  margin-bottom: 2rem;
}
.footer_contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
}
.footer_contact_informations {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-right: 5%;
}
.footer_contact_informations h3 {
  margin: 0rem 0rem 1rem 0rem;
}
.footer_contact_informations_description {
  color: #A5A6FF;
  max-width: 700px;
  margin: 0rem 0rem 1rem 0rem;
}
.footer_contact_informations_data {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: white;
  margin-bottom: 1rem;
}
.footer_contact_informations_data img {
  max-width: 50px;
  height: auto;
  margin-right: 1rem;
}
.footer_contact_informations_data a {
  color: white;
  text-decoration: none;
}
.footer_contact_form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  width: 60%;
  border-radius: 15px;
  background: linear-gradient(to top left, #740CDC, #56ADD5);
}
.footer_contact_form input {
  background: #0F162A;
  font-size: 15px;
  border: none;
  margin-bottom: 0.5rem;
  border-radius: 15px;
  padding: 0.5rem;
  color: white;
}
.footer_contact_form input ::placeholder {
  font-size: 1rem;
  font-weight: bold;
}
.footer_contact_form textarea {
  background: #0F162A;
  border: none;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border-radius: 15px;
  color: white;
  resize: none;
}
.footer_contact_form textarea ::placeholder {
  font-size: 1rem;
  font-weight: bold;
}
.footer_contact_form_sender {
  color: white;
  font-weight: bold;
  width: 150px;
}
.footer_line {
  border: 1px solid white;
  width: 100%;
  height: auto;
  margin: 1rem 0rem 1rem 0rem;
}
.footer_mentions {
  display: flex;
  width: 100%;
  height: auto;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 20px;
}
.footer_mentions_socialMedia {
  display: flex;
}
.footer_mentions_socialMedia img {
  width: 60px;
  height: auto;
  margin: 0rem 0.5rem 0rem 0.5rem;
}

@media screen and (max-width: 1000px) {
  .footer {
    border-radius: 0;
    margin: 0;
  }
  .footer_contact {
    flex-direction: column;
  }
  .footer_contact_informations {
    align-items: center;
    margin: 0;
  }
  .footer_contact_informations_description {
    text-align: center;
    margin: 0rem 0rem 1rem 0rem;
  }
  .footer_contact_form {
    margin-top: 1rem;
    width: 60%;
  }
}